<template>
  <b-container fluid>
        <b-card no-body>
      <b-tabs card>
        <b-tab title="Dados do usuário" active>                    
          <b-row>
            <b-col cols='4'>
              <b-row>
                <label for="input-live" style="text-align: left">Nome:</label>                
              </b-row>
              <b-row>
                <b-form-input
                  id="perfilNome"
                  v-model="nome"
                  :state="nameState"
                  aria-describedby="input-live-help input-live-feedback"
                  placeholder="Informe seu nome"
                  trim
                ></b-form-input>
              </b-row>
            </b-col>
            <b-col cols='4'>
              <label for='compSexo'>Sexo</label>
              <b-form-group v-slot="{ ariaDescribedby }">
                <b-form-radio-group
                  id='compSexo'
                  v-model="sexoSel"
                  :options="sexo"
                  :aria-describedby="ariaDescribedby"
                  name="radio-inline"
                ></b-form-radio-group>
              </b-form-group>              
            </b-col>
          </b-row>  
          <b-row>
            <b-col cols='4'>
              <b-row>
                <label for="perfilNomeUsuario">Login</label>              
              </b-row>
              <b-row>
                <b-input-group prepend="@" >
                  <b-form-input id="perfilNomeUsuario" placeholder="Login" v-model="login"></b-form-input>
                </b-input-group>
              </b-row>
            </b-col>
            <b-col>
              <b-row>
                <label for="perfilSenhaUsuario1" style='margin-left: 15px;'>Senha</label>              
              </b-row>
              <b-row>                   
                <b-col>                    
                  <b-form-input v-model='senha1' type="password" id='perfilSenhaUsuario1' placeholder="Senha"></b-form-input>          
                </b-col>
                <b-col>
                  <b-form-input v-model='senha2' type="password" id='perfilSenhaUsuario2' placeholder="Senha"></b-form-input>
                </b-col>
              </b-row>
            </b-col>
          </b-row>            
          <b-row style='margin-top: 5px;'>
            <b-col>            
              <b-button v-on:click="salvarDados()" variant="outline-success">Salvar Dados</b-button>  
              <b-button v-on:click="buscarDados()" variant="outline-danger">Cancelar</b-button> 
            </b-col>            
          </b-row> 
        </b-tab>
        <!-- <b-tab title="Algo a mais">
          <b-card-text>Tab contents 2</b-card-text>
        </b-tab> -->
      </b-tabs>
    </b-card>    
  </b-container>  
</template>
<script>
import axios from 'axios'
export default {  
  name: "PerfilUsuario", 
  data () {
    return {
      id: 0,
      id_usuario: 0,
      nome: '',
      sexo: [
        { text: 'Masculino', value: 'M' },
        { text: 'Feminino', value: 'F' }
      ],
      sexoSel: 'M',
      login: '',
      senha1: '',
      senha2: ''
    }
  },
  mounted(){
    this.buscarDados()
    
  },
  methods: {
    showToast: function ( variant = null, msg ) {
      this.$bvToast.toast( msg, {
        title: "Aviso",
        variant: variant,
        solid: true
      } )
    },
    buscarDados: function(){      
      axios.get( process.env.ROOT_API.concat('/dadosusuario'),{
        params:  {
          "id": this.$store.getters.id_usuario
        }    
      }).then(result =>{        
        this.nome = result.data.dados.nome_pessoa
        this.sexoSel = result.data.dados.sexo
        this.login = result.data.dados.login
      }).catch(err => {
        this.showToast("warning", err)
      })
    },
    salvarDados: function(){
      axios.post(process.env.ROOT_API.concat('/atualizardadosusuario'),{    
        "id_usuario":this.$store.getters.id_usuario,
        "login":this.login,
        "nome": this.nome,
        "senha":"U2FsdGVkX19Gzk22WdqioHzRLrQF61iYO90+UPk2aMw=",
        "sexo": this.sexoSel
      }).then(result => {
        this.buscarDados()        
        this.showToast("success", result.data.msg)
      }).catch(err => {
        this.showToast("warning", err)
      })
    }
  }
}
</script>



