<template>
  <div>
    <SideBar  :btnClickHandler="this.addMarcacao" nome="teste" />
    <Marcador tipo="GERAL" :actExcluir="excluir" :id="'marc_'+ item" :numero="item"  v-for="item in listMarc" :key="item"/>
    <b-img v-bind:src="this.arquivo" fluid  />
  </div>
</template>
<script>
import Marcador from "../components/Marcador"
import SideBar from "../components/SideBar"
import axios from 'axios'

export default {  
  name: "Correcao",
  data ( ) {
    return {
      listMarc: [],
      numero: 0,
      arquivo: ''
    }
  },
  components: {
    Marcador,
    SideBar
  },
  mounted () {    
    this.buscarCorrecao(this.$route.params.idCorrecao)
  },
  methods: {
    addMarcacao: function ( ) {
      let vm = this
      //this.numero += 1
      //this.listMarc.push( this.numero )   
      axios.post(process.env.ROOT_API.concat('/setmarcador'), {
          "x":100,
          "y":10,
          "width":120,
          "height":50,
          "marcacao_item":2,
          "id_correcao":vm.$route.params.idCorrecao          
      }).then(dados => {
        // console.log(dados.data.id)
        this.listMarc.push(dados.data.id)
      }).catch(err => {
        console.log(err)
      })
    },
    excluir: function () {
      console.log( "alex" )
    },
    buscarCorrecao: function(aID) {      
      var vm = this      
      axios.get(process.env.ROOT_API.concat('/dadoscorrecao'),{
        params: {
          id: aID
        }
      }).then(result => {        
        vm.arquivo =  result.data.dados.arquivo           
        axios.get(process.env.ROOT_API.concat('/getmarcadores'),{
          params: {
            id_correcao: aID
          }
        }).then(result => {   
          let dados = result.data.dados
          for(let i = 0;i <= dados.length;i++){   
            vm.listMarc.push(dados[i].id)
          }
        }).catch(err => {
          console.log(err)
        })
      }).catch(err=>{
        alert(err)
      })
    }
  },  
  
}
</script>



