<template>    
                                                                              <!-- tentar rever para fazer condicionar -->
    <b-navbar toggleable="lg" type="light"  style="background: #A7DBDB;" fixed="top" sticky>    
      <b-navbar-brand  v-b-toggle href="#sidebar-1" @click.prevent>
          <b-icon icon='list'></b-icon>
      </b-navbar-brand>
      <b-navbar-brand href="#">
          EntreLinhas
      </b-navbar-brand>
      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav>
          <b-nav-item href="#/">Home</b-nav-item>
          <!-- <b-nav-item href="#/cadastrocorrecoes" v-if="this.isLogged">Cadastrar Correções</b-nav-item>
          <b-nav-item href="#/correcoes" v-if="this.isLogged">Correções</b-nav-item> -->
        
        <b-dropdown size="sm" variant="label" toggle-class="text-decoration-none" no-caret  v-if="this.isLogged">
          <template #button-content>
              <b-label style="color: rgba(0, 0, 0, 0.5);">Correções</b-label>
          </template>           
          <b-dropdown-item-button v-on:click="abrirPagina('cadastrocorrecoes')">Cadastrar</b-dropdown-item-button>
          <b-dropdown-item-button v-on:click="abrirPagina('correcoes')">Localizar</b-dropdown-item-button>
        </b-dropdown>
      </b-navbar-nav>
        
      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto" v-if="this.isLogged == false">
          <b-form-input v-model="usuario" size="sm" class="mr-sm-2" type="text" placeholder="Usuário"></b-form-input>
          <b-form-input v-model="senha" v-on:keyup.enter='login()' size="sm" class="mr-sm-2" type="password" placeholder="Senha"></b-form-input>
          <b-button size="sm" v-on:click="login()" class="my-2 my-sm-0" variant='success'>Login</b-button>
      </b-navbar-nav>
      <b-navbar-nav class="ml-auto" v-else>
          <b-dropdown size='md' right :text='msgExibition' variant='outline'>
          <b-dropdown-item-button v-on:click='perfil()'>
            <b-icon icon="person-circle" aria-hidden="true"></b-icon>
            Perfil <span class="sr-only"></span>
          </b-dropdown-item-button>                    
          <b-dropdown-divider></b-dropdown-divider>
          <b-dropdown-item-button>Mensagens</b-dropdown-item-button>          
          <b-dropdown-divider></b-dropdown-divider>
          <b-dropdown-item-button variant="danger"
            v-on:click='logout()'
          >
            <b-icon icon="x-circle" aria-hidden="true"></b-icon>
              Sair
          </b-dropdown-item-button>
        </b-dropdown>        
      </b-navbar-nav>
      </b-collapse>
    </b-navbar>
</template>
<script>
import axios from "axios"

export default {
  name: "NavBar",
  data: function ( ) {
    return {
      usuario: "",
      senha: "",
      isLogged: false,
      userName: "",
      userSex: "",
      MsgAviso: "",
      msgExibition: ""
    }
  },
  mounted () {
    console.log( this.$session.get('_SessEntreLinhas') )
    
  },
  methods: {
    showToast: function ( variant = null, msg ) {
      this.$bvToast.toast( msg, {
        title: "Aviso",
        variant: variant,
        solid: true
      } )
    },
    abrirPagina: function(value){
      this.$router.push( { path: `/${value}` } )
    },
    login: function () {          
      axios.get( process.env.ROOT_API.concat( "/login" ),
        {
          params: {
            "login": this.usuario,
            "pass": this.senha          
          }
        }).then( result => {
        this.isLogged = result.data.result
        if ( !this.isLogged ) {
          this.showToast( "warning", result.data.msg || "Verique os dados informados." )
          this.MsgAviso( result.data.msg )   
        } else {
          this.userName = result.data.pessoa.NOME_PESSOA
          this.userSex = result.data.pessoa.SEXO          
          this.$session.start()
          console.log(result.data)
          this.$session.set('_SessEntreLinhas',result.data.token)          
          if ( this.userSex == "M" ) {
            this.msgExibition = `Bem vindo ${this.userName}`
          } else {
            this.msgExibition = `Bem vinda ${this.userName}`
          }
          let param = JSON.stringify({
            "id_usuario": result.data.pessoa.ID_USUARIO,
            "id_pessoa": result.data.pessoa.ID_PESSOA
          })
          //this.$emit('atualizar-usuarios', param)
          this.$store.commit('setIdUsuario', result.data.pessoa.ID_USUARIO)
          this.$store.commit('setIdPessoa', result.data.pessoa.ID_PESSOA)
        }
      } ).catch( err => {
        console.log( err )
      } )
    },
    logout: function () {
      this.isLogged = false
      this.usuario = ""
      this.senha = ""
      this.$session.destroy()
      this.$router.push( { path: "/" } )
    },
    perfil: function () {
      this.$router.push( { path: "/perfilusuario" })
    }
  }
}
</script>



