import Vue from "vue"
import Router from "vue-router"
import auth from '../middleware/auth';
import VueRouteMiddleware, { middleware } from 'vue-route-middleware';

import Home from "../pages/Home"
import CadastroCorrecoes from "../pages/CadCorrecoes"
import PerfilUsuario from "../pages/PerfilUsuario"
import Correcao from "../pages/Correcao"
import Correcoes from '../pages/ListaCorrecoes'

import Teste from "../pages/Teste"


Vue.use( Router )

const routes = [
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/teste",
      name: "Teste",
      componente: Teste 
    },
    {
      path: "/cadastrocorrecoes",
      name: "CadastroCorrecoes",
      component: CadastroCorrecoes ,    
    },
    {
      path: "/perfilusuario",
      name: "Perfil",
      component: PerfilUsuario      
    },
    {
      path: "/correcao/:idCorrecao",
      name: "Correcao",
      component: Correcao      
    },
    {
      path: "/correcoes",
      name: "Correcoes",
      component: Correcoes      
    }
]

const router = new Router({
  // mode: 'history',
  // base: 'http://localhost:8080/',
  routes
})
router.beforeEach((to, from, next) => {
  
  const reqSession = to.matched.some(route => route.meta.requiresSession)
  // if (!reqSession) next()  
  if (router.app.$session.exists('_SessEntreLinhas')) {   
    // axios.get(process.env.ROOT_API.concat('/rotacliente'),{})       
    console.log('tem')
    next()    
  } else {
    console.log('naotem')    
    router.app.$session.destroy()
    next()
    
    // window.location('/')
    // next({ name: 'Home' })    
    // router.push('Home')
  }

})
export default router

