<template>
    <vue-draggable-resizable :w="this.width" :h="this.height" :x="x" :y="y" @dragstop="stopDrag" @resizestop="stopResize" :parent="true">
      <div style="height:100%; width:100%" v-bind:class="tipo" >
        <b-row>
          <b-col>
            {{this.numero}}
          </b-col>
          <b-col>
            <b-dropdown size="sm"  variant="link" toggle-class="text-decoration-none" no-caret >
                <template v-if="this.iconWhite" #button-content>
                    <b-icon icon="gear-fill" font-scale="1.2" style="color: white"></b-icon>
                </template>
                <template v-else #button-content>
                    <b-icon icon="gear-fill" font-scale="1.2" style="color: black"></b-icon>
                </template>
                <b-dropdown-header>Opcoes</b-dropdown-header>
                <b-dropdown-divider />
                <b-dropdown-item v-on:click="buscar('O')"> Ortografia </b-dropdown-item>
                <b-dropdown-item v-on:click="buscar('G')"> Geral </b-dropdown-item>
                <b-dropdown-item v-on:click="buscar('R')"> Regência </b-dropdown-item>
                <b-dropdown-item v-on:click="buscar('S')"> Semântica </b-dropdown-item>
                <b-dropdown-item v-on:click="buscar('C')"> Concordância </b-dropdown-item>
                <b-dropdown-divider />                
                <b-dropdown-item v-b-modal="`modalObs${numero}`" > Observação </b-dropdown-item>
                <!-- <b-button v-on:click="abrirObservacao">teste</b-button> -->
                <b-dropdown-divider />
                <b-dropdown-item v-on:click="removerMarcador(numero)" > Excluir </b-dropdown-item>
            </b-dropdown>
          </b-col>
        </b-row>  
        <template>
          <b-modal :id="`modalObs${numero}`" @ok="atualizarDados()" title="Observação" ok-only>
            <b-form-textarea v-model="observacao"></b-form-textarea>
            <!-- <b-button variant="outline-primary" @click="$bvModal.hide(`modalObs${numero}`)">Ok</b-button> -->
          </b-modal>
        </template>          
      </div>
    </vue-draggable-resizable>     

</template>
<script>
import VueDraggableResizable from "vue-draggable-resizable"
import axios from "axios"

export default {
  name: "Marcador",
  props: {
    numero: Number,
  },
  data: function ( ) {
    return {
      id:0,
      width: 0,
      height: 0,
      x: 20,
      y: 20,
      observacao: '',
      marcacao_item: 1,
      // Ortografia	  -	1
      // Geral		    -	2
      // Regência	    -	3
      // Semântica	  -	4
      // Concordância	-	5
      ORTOGRAFIA: "ORTOGRAFIA", 
      GERAL: "GERAL",
      REGENCIA: "REGENCIA",
      SEMANTICA: "SEMANTICA",
      CONCORDANCIA: "CONCORDANCIA",
      cor: this.CONCORDANCIA,
      iconWhite: "",
      tipo: "GERAL"
    }
  },
  mounted () {    
    this.buscaDadosMarcador()
  },
  methods: {      
    buscar ( pTipo ) {
      this.tipo = this.verificaTipo( pTipo )
      this.atualizarDados()
    },
    atualizarDados(){           
      let vm = this    
      axios.post(process.env.ROOT_API.concat('/setmarcador'), {
        "x":vm.x,
        "y":vm.y,   
        "width":vm.width,
        "height":vm.height,       
        "id_correcao":vm.$route.params.idCorrecao,
        "id_marcacao":vm.numero,
        "marcacao_item": vm.marcacao_item,
        "observacao": vm.observacao
      }).then(dados => {
        console.log(dados)
      }).catch(err => {
        console.log(err)
      })
    },
    stopResize: function ( x, y, width, height ) {
      let atualizar = false 
      if(this.x != x){
        atualizar = true
      }else if(this.y != y){
        atualizar = true
      }else if(this.width != width){
        atualizar = true
      }else if(this.height != height){
        atualizar = true
      }
      this.x = x
      this.y = y
      this.width = width
      this.height = height
      if(atualizar){
        this.atualizarDados()
      }
    },
    stopDrag: function ( x, y ) {     
      let atualizar = false
      if(this.x != x) {
        atualizar = true
      }else if(this.y != y){
        atualizar = true
      }
      this.x = x
      this.y = y
      if(atualizar){
        this.atualizarDados()
      }
    },
    verificaTipo: function ( tipo ) {
      if ( tipo === "O" ) {
        this.iconWhite = "S"
        this.marcacao_item = 1
        return this.ORTOGRAFIA
      } else if ( tipo === "G" ) {
        this.iconWhite = ""
        this.marcacao_item = 2
        return this.GERAL
      } else if ( tipo === "R" ) {
        this.iconWhite = "S"
        this.marcacao_item = 3
        return this.REGENCIA
      } else if ( tipo === "S" ) {
        this.iconWhite = "S"
        this.marcacao_item = 4
        return this.SEMANTICA
      } else {
        this.marcacao_item = 5
        this.iconWhite = "S"
        return this.CONCORDANCIA
      }      
    },
    removerMarcador: function ( pId ) {
      let vm = this            
      axios.post(process.env.ROOT_API.concat('/deletemarcador'),{        
        id: pId
      }).then(dados => {             
        if(dados.data.result){
          document.getElementById( "marc_" + pId ).remove()
        }
      }).catch(err => {
        console.log(err)
      })
    },
    buscaDadosMarcador: function(){
      let vm = this
      axios.get(process.env.ROOT_API.concat('/getmarcador'),{
        params:{
          id: vm.numero
        }
      }).then(result =>{            
        let dados = result.data.dados
        vm.height = dados.tam_h
        vm.width = dados.tam_w
        vm.x = dados.pos_x
        vm.y = dados.pos_y
        vm.marcacao_item = dados.marcacao_item
        vm.observacao = dados.observacao
        switch(dados.marcacao_item){
          case 1:
            vm.tipo = vm.ORTOGRAFIA
            break;
          case 2:
            vm.tipo = vm.GERAL
            break;
          case 3:
            vm.tipo = vm.REGENCIA
            break;
          case 4:
            vm.tipo = vm.SEMANTICA
            break;
          case 5:
            vm.tipo = vm.CONCORDANCIA
            break;  
        }
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>
<style>
  .ORTOGRAFIA {
      background-color: rgb(248, 3, 21,0.5);
      color: white;
      font-weight: bold;
  }
  .GERAL {
      background-color: rgb(254, 224, 21, 0.5);
      color: black;
      font-weight: bold;
  }
  .REGENCIA {
      background-color: rgb(252, 119, 2, 0.5);
      color: white;
      font-weight: bold;
  }
  .SEMANTICA {
      background-color: rgb(0, 185, 21, 0.5);
      color: white;
      font-weight: bold;
  }
  .CONCORDANCIA {
      background-color: rgb(199, 43, 234, 0.5);
      color: white;
      font-weight: bold;
  }
</style>



