import Vue from "vue"
import App from "./App"
import router from "./router"
import { BootstrapVue, IconsPlugin } from "bootstrap-vue"
import "bootstrap/dist/css/bootstrap.css"
import "bootstrap-vue/dist/bootstrap-vue.css"
import VueDraggableResizable from "vue-draggable-resizable"
import "vue-draggable-resizable/dist/VueDraggableResizable.css"
import 'es6-promise/auto'
import {store} from './store/store'
import VueSession from 'vue-session'
import Axios from 'axios'

var refresh = ''
const responseHandler = response => {
  if (response.status === 401) {
    JSON.parse(sessionStorage['vue-session-key'])['_SessEntreLinhas'] = ''
      // window.location = '/';
  }
  
  // console.log(response.data)
  refresh = response.data['_sessEntreLinhasRefresh']
  JSON.parse(sessionStorage['vue-session-key'])['_SessEntreLinhas'] = refresh
  return response;
};

const errorHandler = error => {
  //sessionStorage.clear()
  // sessionStorage.removeItem('vue-session-key')
  // console.log(JSON.parse(sessionStorage['vue-session-key']))
  JSON.parse(sessionStorage['vue-session-key'])['_SessEntreLinhas'] = ''
  // sessionStorage.clear()
  window.location = '/';
  console.log('error')
  return Promise.reject(error);
};

Axios.interceptors.request.use((config) => {
  
  config.headers = {
                      'x-access-token': JSON.parse(sessionStorage['vue-session-key'])['_SessEntreLinhas'],
                      'Content-type': 'application/json'
                    }  
  return config
},err => {
  return Promise.reject(err)
})

Axios.interceptors.response.use(
  (response) => responseHandler(response),
  (error) => errorHandler(error)
);

Vue.config.productionTip = false
Vue.use( BootstrapVue )
Vue.use( IconsPlugin )
Vue.use(VueSession)
//Vue.use(a)

Vue.component( "vue-draggable-resizable", VueDraggableResizable )

// eslint-disable-next-line
new Vue( {
  el: "#app",
  router,
  store,
  components: { App },  
  template: "<App/>"
} )


