<template>
  <div id="app">
    <!-- https://qastack.com.br/programming/40915436/vuejs-update-parent-data-from-child-component -->
    <NavBar @atualizar-usuarios="atualizarUsuarios" :id_pessoa="id_pessoa" :id_usuario="id_usuario" />
    <router-view :id_pessoa="id_pessoa" :id_usuario="id_usuario" />
  </div>
</template>

<script>
import NavBar from "./components/NavBar"

export default {
  name: "App",
  components: {
    NavBar
  },
  data(){
    return {
    id_usuario: 0,
    id_pessoa: 0  
  }},
  methods:{
    atualizarUsuarios(e){
      e = JSON.parse(e)
      this.id_usuario = e.id_usuario || 0
      this.id_pessoa = e.id_pessoa || 0      
    }
  }
}
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;

  -webkit-font-smoothing: antialiased;

  -moz-osx-font-smoothing: grayscale;

  text-align: center;

  color: #2c3e50;

}
</style>



