<template>
  <b-container>
    <b-row>
      <b-col>
        <h1>Cadastro de Correções</h1>
      </b-col>
    </b-row>
    <b-row>      
      <b-col>            
        <b-row>
          <label for="input-titulo" style="text-align: left">Titulo:</label>  
        </b-row>    
        <b-row>          
          <b-input-group>
              <b-form-input id="input-titulo" v-model="titulo"  ></b-form-input>          
              <b-button-group>                                      
                <b-button variant="outline-primary"   v-b-modal.modal-buscCorrecoes>
                  <b-icon icon="search" aria-label="Help"></b-icon>
                </b-button>            
              </b-button-group> 
          </b-input-group>
        </b-row>
        <b-row>
          <label for="input-descr" style="text-align: left">Descrição:</label>
          <b-form-textarea id="input-descr" v-model="descricao"></b-form-textarea> 
        </b-row>
        <b-row>          
          <label for="input-arq" style="text-align: left">Arquivo:</label>
          <b-form-file @change="onFileChange" id="input-arq" v-model="arquivo"></b-form-file> 
        </b-row>
        <b-row style='margin-top: 5px;'>
          <b-col >
            <b-button variant="success" v-on:click="salvarCorrecao()">
              Salvar
            </b-button>
                
            <b-button variant="danger" v-on:click="cancelar()">
              Cancelar
            </b-button>
          </b-col>
        </b-row>
      </b-col>
    </b-row>   

    <b-modal id="modal-buscCorrecoes" title="Buscar Correções" ok-only>   
      <b-container>   
        <b-row>
          <b-col sm="9">
            <b-form-input v-model="modalTitulo"></b-form-input>
          </b-col>
          <b-col sm="2">
            <b-button v-on:click="buscarCorrecoes()">Buscar</b-button>
          </b-col>
        </b-row>
        <b-row>
          <b-table striped hover 
            :items="items"          
            selectedClass="table-info"
            @row-dblclicked="selecionaCorrecao"            
          ></b-table>
        </b-row>
      </b-container>
    </b-modal>    
  <!-- <b-button v-on:click="getBase64">teste</b-button> -->
  </b-container>
  
</template>
<script>
import axios from 'axios'
export default {
  name: "Correcoes",
  data () {
    return {
      id: '',
      modalTitulo: '',
      titulo:'',
      descricao: '',
      arquivo: '',
      items: [
         
      ],
      baseImage: null
    }
  },
  beforeCreate: function(){    
    axios.get(process.env.ROOT_API.concat('/rotacliente'),{})
  },
  methods: {
    showToast: function ( variant = null, msg ) {
      this.$bvToast.toast( msg, {
        title: "Aviso",
        variant: variant,
        solid: true
      } )
    },
    limparTela: function(){
      this.id = null
      this.titulo = ''
      this.descricao = ''
      this.arquivo = null
      this.items = []
    },
    buscarCorrecoes: function() {
      var vm = this      
      axios.get(process.env.ROOT_API.concat('/buscacorrecoes'),{
        params: {
          titulo: this.modalTitulo
        }
      }).then(result => {  
        this.items = []      
        let dados = result.data.dados
        
        for(let i= 0; i < dados.length; i++){
          vm.items.push({
            codigo: dados[i].id,                       
            titulo: dados[i].titulo,
            usuario: dados[i].usuario.pessoa.nome_pessoa
          })
        }
      }).catch(err => {
        if(err.response.data == "Unauthorized"){
          this.$router.push( { path: "/" } )
        }
      })
    },
    selecionaCorrecao: function(item, index) {    
      var vm = this
      axios.get(process.env.ROOT_API.concat('/dadoscorrecao'),{
        params: {
          id: item.codigo
        }
      }).then(aDados => {
        
        let dados = aDados.data.dados
        vm.id = dados.id
        vm.titulo = dados.titulo
        vm.descricao = dados.descricao
        vm.arquivo = dados.arquivo
        vm.$bvModal.hide('modal-buscCorrecoes');        
      }).catch(err => {
        this.showToast("warning", err)
      })
    },
    salvarCorrecao: function(){
      var vm = this            
      axios.post(process.env.ROOT_API.concat('/setcorrecoes'), {        
        "id_correcao": vm.id || null,
        "titulo": vm.titulo || null,
        "descricao": vm.descricao || null,
        "arquivo": vm.baseImage || null,
        "usuarioId": this.$store.getters.id_usuario
      }).then(dados => {
        this.showToast("success", "Correção Cadastrada")
        this.limparTela()
      }).catch(err => {
        this.showToast("warning", err)
      })
    },
    buscarDados: function(){
      var vm = this
      axios.get(process.env.ROOT_API.concat('/dadoscorrecao'),{
        params: {
          id: vm.id
        }
      }) 
    },
    cancelar: function(){
      this.id = 0
      this.titulo = ''
      this.descricao = ''
      this.arquivo = null
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;      
      if (!files.length)
        return;
      this.createImage(files[0]);
    },
    createImage(file) {
      
      //var image = new Image();
      var reader = new FileReader();
      var vm = this;

      reader.onload = (e) => {        
        vm.baseImage = e.target.result;      
      };      
      reader.readAsDataURL(file);      
    }   
  }
}
</script>


