<template>
  <b-container>
    <b-row>
      <b-col> <h1> Lista de Correções </h1> </b-col>
    </b-row>
    <b-row>
      <b-input-group>
          <b-form-input id="input-titulo" v-model="tituloCorrecao"  ></b-form-input>          
          <b-button-group>                                      
            <b-button variant="outline-primary" @click="buscarCorrecoes()">
              <b-icon icon="search" aria-label="Help"></b-icon>              
            </b-button>            
          </b-button-group> 
      </b-input-group>        
    </b-row>
    <b-row>
      <b-col>
        <label for="dtInicio">Data inicio</label>
        <b-form-datepicker id="dtInicio" v-model="dtInicio" ></b-form-datepicker>
      </b-col>
      <b-col>
        <label for="dtFim">Data Final</label>
        <b-form-datepicker id="dtFim" v-model="dtFim" ></b-form-datepicker>
      </b-col>
    </b-row>
    <br>
    <b-row>
      <b-table :items="items" :fields="fields" striped>
        <template #cell(opções)="row">
          <b-button variant="outline-primary" size="sm" @click="abrirCorrecao(row.item.codigo)" class="mr-2">
              <b-icon icon="folder2-open" aria-label="Help"></b-icon>              
            </b-button>        
        </template>            
      </b-table>        
    </b-row>
  </b-container>
</template>
<script>
import moment from 'moment'
import axios from 'axios'

export default {
  name: 'ListaCorrecoes',
  data () {
    return {
      tituloCorrecao: '',
      dtInicio: '', 
      dtFim: '',
       fields: ['titulo', 'dataCriacao', 'opções'],
      items:[]
    }
  },
  methods: {
    abrirCorrecao (codigo) { 
      this.$router.push( { path: `/correcao/${codigo}` } )
    },
    buscarCorrecoes () {      
      // moment(this.dtInicio).format('yyyy-MM-DD')
      // moment(this.dtFim).format('yyyy-MM-DD')
      // this.items = [
      //   {
      //     codigo: 3, titulo: 'Alex', dataCriacao: moment('2020-02-21').format('DD/MM/yyyy')
      //   }
      // ]
      var vm = this      
      axios.get(process.env.ROOT_API.concat('/buscacorrecoes'),{
        params: {
          titulo: this.tituloCorrecao,
          dtInicio: moment(this.dtInicio).format('yyyy-MM-DD'),
          dtFim: moment(this.dtFim).format('yyyy-MM-DD')
        }
      }).then(result => {  
        this.items = []        
        let dados = result.data.dados
        
        for(let i= 0; i < dados.length; i++){
          vm.items.push({
            codigo: dados[i].id,                       
            titulo: dados[i].titulo,
            dataCriacao: moment(dados[i].dtCriacao).format('DD/MM/yyyy')
          })
        }
      }).catch(err => {
        if(err.response.data == "Unauthorized"){
          this.$router.push( { path: "/" } )
        }
      })
    }
  },
  created () {
    let dataIni = new Date()
    let dataFim = new Date()
    
    this.dtInicio = dataFim
    this.dtFim = dataIni
    dataFim.setMonth( dataFim.getMonth()-1 )
  }
}
</script>


