import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
//teste
export const store = new Vuex.Store({
  state: {
    id_usuario: 0,
    id_pessoa: 0
  },
  mutations: {
    setIdUsuario(state, value) {
      state.id_usuario = value
    },
    setIdPessoa(state, value) {
      state.id_pessoa = value
    }
  },
  getters: {
    id_usuario: state => state.id_usuario,
    id_pessoa: state => state.id_pessoa
  }
})


